
import { defineComponent } from "vue";
import Tree from "@/shared/components/tree/tree/tree.vue";
import TreeDataService from "@/shared/services/TreeDataService";
import { COM20600211_REQ_BODY, COM20600211_RES } from '@/shared/type/COM20600211.type';
import { COM20600212_REQ_BODY, COM20600212_RES, EMPLOYEELIST } from '@/shared/type/COM20600212.type';
import { COM20600221_REQ_BODY, COM20600221_RES } from '@/shared/type/COM20600221.type';
import { COM20600241_REQ_BODY, COM20600241_RES } from '@/shared/type/COM20600241.type';
import { COM20600112_REQ, COM20600112_RES } from '@/shared/type/COM20600112.type';
import { modalController } from '@ionic/vue';
import Pagination from '@/shared/components/Pagination/Pagination.vue'
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue';
import { APM20200113_RES } from '@/shared/type/APM20200113.type';
import { screenMode } from '@/shared/type/Approval.type';
const treeService = new TreeDataService();
export default defineComponent({
    name: "com206002sc00",
    components: {
        Tree,
        Pagination,
        BreadCrumb
    },

    data() {

        return {
            isReset: false,
            roleTree: [] as any,
            roleList: [] as any,
            selectedRole: {} as any,
            isAddService: false,
            linkToRole: "",
            serviceList: [] as any,
            searchByServiceID: '' as string,
            searchServiceIDName: '' as string,
            checkList: [] as any,
            isCheckAll: false as boolean,
            userRoleID: '' as string,
            tmpServiceList: [] as EMPLOYEELIST[],
            roleListForRemove: [] as any,
            isGroupData: false as boolean,
            roleServiceList: [] as any,
            currentPage: 1,
            pageSize: 5,
            startingIndex: 1,
            totalScreenItems: 0,
            totalRoleList: 0 as number,
            roleSize: 500 as number,
            roleID: '' as string,
            approvalRes: {} as any,
            operationMode: 'normal' as screenMode,
            cloneServiceRoleData: [] as any,
            isApprovalDeletedStatus: false as boolean
        };
    },

    mounted() {
        this.onGetRoleList();
        this.onModifyList();
    },

    computed: {
        disableMainCheckBox(): boolean {
            return this.serviceList.every((item: any) => item.linkedStatus == 'Y') as any;
        },
        checkAllServiceList(): any {
            if (this.isCheckAll) {
                this.serviceList.forEach((item: any) => {
                    return item.checked = true;
                })
            } else {
                if (this.serviceList.length === this.checkList.length) {
                    this.serviceList.forEach((item: any) => {
                        return item.checked = false;
                    })
                }
            }
            return this.serviceList
        },
        convertLinkedStatus() {
            return (linkedStatus: string) => linkedStatus == 'Y' ? `${this.$t('com206002sc00.linked')}` : `${this.$t('com206002sc00.not_linked')}`
        },
        approvalType(): string {
            return this.$route.query.approvalType ? this.$route.query.approvalType.toString() : ''
        },
    },

    methods: {
        async onGetRoleList() {
            const reqBody: COM20600211_REQ_BODY = {
                pageNumber: 1,
                pageSize: 1500,
            };
            const responseData: any = await this.$request.getRequest("COM20600211", reqBody, false) as COM20600211_RES;
            if (responseData.header.result) {
                this.roleList = responseData.body.list;
                this.transformAndExpandTreeData(this.roleList);
                this.selectedRole = this.roleList;
                this.onHighLightParentNode();
                this.approvalRequest();
            }
        },


        transformAndExpandTreeData(dataList: Array<any>) {
            const transformData = treeService.transformIntoTreeServiceRole(dataList, 'userRoleID', 'parentUserRoleID', 'userRoleName');
            this.roleTree = transformData;
            treeService.expandAllNodes(this.roleTree);
        },


        async onSelectRoleList(value) {
            if (this.approvalType === '') {
                if (this.isAddService) {
                    this.diabledSelectNewService();
                    this.onHighLightParentNode();
                    return
                }
                if (this.selectedRole) {
                    const reqBody: COM20600211_REQ_BODY = {
                        pageNumber: 1,
                        pageSize: 1,
                    };
                    const responseData: any = await this.$request.getRequest("COM20600211", reqBody, false) as COM20600211_RES;
                    if (responseData.header.result) {
                        this.selectedRole = value;
                        this.linkToRole = `${this.selectedRole.userRoleID}: ${this.selectedRole.userRoleName}`;
                        this.userRoleID = this.selectedRole.userRoleID;
                        this.roleID = this.selectedRole.userRoleID
                        if (this.selectedRole.serviceID) {
                            this.getServiceDetails(this.selectedRole.serviceID);
                        }
                        this.searchByServiceID = ''
                        this.searchServiceIDName = ''
                    }
                }
            } else {
                this.selectedRole = value;
                let isCallingDetail = false;
                console.log("cloneServiceRoleData", this.cloneServiceRoleData);
                this.cloneServiceRoleData.find((item) => {
                    if (item.serviceID == this.selectedRole.serviceID && item.parentUserRoleID == this.selectedRole.parentUserRoleID) {
                        isCallingDetail = true;
                    } else {
                        this.selectedRole.state.selected = false;
                    }
                })
                this.apprrovalRoleActive();
                if (isCallingDetail) {
                    const reqBody: COM20600211_REQ_BODY = {
                        pageNumber: 1,
                        pageSize: 1,
                    };
                    const responseData: any = await this.$request.getRequest("COM20600211", reqBody, false) as COM20600211_RES;
                    if (responseData.header.result) {
                        this.selectedRole = value;
                        this.linkToRole = `${this.selectedRole.userRoleID}: ${this.selectedRole.userRoleName}`;
                        this.userRoleID = this.selectedRole.userRoleID;
                        this.roleID = this.selectedRole.userRoleID;
                        if (this.selectedRole.serviceID) {
                            this.getServiceDetails(this.selectedRole.serviceID);
                        }
                    }
                }
                this.linkToRole = ''
                this.searchByServiceID = ''
                this.searchServiceIDName = ''
            }

        },


        async getServiceDetails(serviceID) {
            const reqBody: COM20600112_REQ = {
                serviceID: serviceID
            };
            const responseData: any = await this.$request.getRequest("COM20600112", reqBody, false) as COM20600112_RES;
            if (responseData.header.result) {
                let service = this.roleList.find((item: any) => item.userRoleID == this.selectedRole.parentUserRoleID);
                this.linkToRole = `${service.userRoleID}: ${service.userRoleName}`
                this.searchServiceIDName = `${responseData.body.serviceIDName}`
                this.searchByServiceID = responseData.body.serviceID
            }
        },


        statusConverter(statusCode): string {
            switch (statusCode) {
                case 'Linked':
                    return 'active';

                default:
                    return 'inactive';
            }
        },


        initNewRole() {
            return {
                isTempNewRole: true,
                userRoleID: "",
                parentUserRoleID: this.selectedRole.userRoleID,
                userRoleName: "New Service..",
                nodes: [],
                state: { indeterminate: false, checked: false, selected: true, expanded: false, highLightParentNode: false }
            }
        },


        resetStateTreeData() {
            if (this.roleList.length === 0) return
            this.roleList = this.roleList.map((role: any) => {
                if (role.state) {
                    role.state.selected = false
                }
                return role
            })
        },


        onClickAddService() {
            if (this.selectedRole.nodes) {
                if (this.selectedRole.parentUserRoleID == '') {
                    this.isAddService = true;
                    this.resetStateTreeData();
                    this.roleList = [...this.roleList, this.initNewRole()] as any
                    this.transformAndExpandTreeData(this.roleList)
                    this.selectedRole = { ...this.initNewRole(), userRoleName: "" } as any;
                    this.onHighLightParentNode();
                    this.onGetServiceList();
                } else {
                    this.$dialog.onOpen({
                        dialogMode: "Warning",
                        content: this.$t('com206002sc00.currently_you_selected_service'),
                        callback: () => modalController.dismiss()
                    })
                }
            } else {
                this.$dialog.onOpen({
                    dialogMode: "Warning",
                    content: this.$t('com206002sc00.please_select_specific_list_before_add'),
                    btnLabel: this.$t('common.check'),
                    showCancel: false,
                    callback: () => modalController.dismiss()
                })
            }
            this.roleListForRemove = [];
        },


        onModalCancel() {
            if (this.checkList.length > 0) {
                this.$dialog.onOpen({
                    dialogMode: "Question",
                    content: this.$t('message.are_you_sure_you_want_to_cancel_without_saving_data'),
                    callback: () => {
                        this.onClickCancel();
                    }
                })
            } else {
                this.onClickCancel();
            }
        },


        onClickCancel() {
            this.roleList = this.roleList.filter((role: any) => !role.isTempNewRole);
            this.transformAndExpandTreeData(this.roleList)
            this.isAddService = false;
            modalController.dismiss();
            this.onResetCheckedList();
            this.onResetHighLightParentNode();
            this.selectedRole = this.nodeSelectAtRoot()
            this.linkToRole = ''
            this.searchByServiceID = ''
            this.searchServiceIDName = ''
        },


        nodeSelectAtRoot() {
            return {
                userRoleID: "",
                userRoleName: "",
                parentUserRoleID: "",
                state: { indeterminate: false, checked: false, selected: false, expanded: false, highLightParentNode: false }
            }
        },


        onResetCheckedList() {
            this.serviceList.forEach((list) => {
                return list.checked = false
            })
            this.checkList = [];
            this.isCheckAll = false;
        },


        diabledSelectNewService() {
            // Disabled select for all Tree list when have temp new role
            this.roleList = this.roleList.map((role: any) => {
                if (role.isTempNewRole) {
                    role.state = { indeterminate: false, checked: false, selected: true, expanded: false }
                } else {
                    role.state = { indeterminate: false, checked: false, selected: false, expanded: false }
                }
                return role
            });
            this.transformAndExpandTreeData(this.roleList)
        },


        async onGetServiceList() {
            const reqBody: COM20600212_REQ_BODY = {
                serviceID: this.searchByServiceID != '' ? this.searchByServiceID : '',
                serviceIDName: this.searchServiceIDName != '' ? this.searchServiceIDName : '',
                pageNumber: this.currentPage,
                pageSize: this.pageSize,
                userRoleID: this.roleID
            };
            const responseData = await this.$request.getRequest("COM20600212", reqBody, false) as COM20600212_RES;
            if (responseData.header.result) {
                this.serviceList = responseData.body.list as any;
                this.tmpServiceList = this.serviceList;
                this.totalScreenItems = responseData.body.totalCount as any;
            }
        },


        onModifyList() {
            this.tmpServiceList.map((items: any) => {
                this.serviceList.push({
                    checked: false,
                    serviceID: items.serviceID,
                    serviceIDName: items.serviceIDName,
                })
                return this.serviceList
            }) as any
        },


        onClickSearchServiceList() {
            if (this.searchServiceIDName == "" && this.searchByServiceID == '') {
                this.pageSize = 5;
                this.currentPage = 1;
                this.isReset = true;
                this.onGetServiceList();
            } else {
                this.currentPage = 1;
                this.isReset = true
                this.onGetServiceList();
            }
        },


        resetServiceList() {
            this.pageSize = 5;
            this.currentPage = 1;
            this.isReset = true;
            this.searchByServiceID = '';
            this.searchServiceIDName = '';
            this.onGetServiceList();
        },


        checkServiceList(item) {
            if (item.checked) {
                this.checkList.push({
                    serviceID: item.serviceID,
                    userRoleID: this.userRoleID
                });
            } else {
                const index = this.checkList.findIndex(e => e.serviceID === item.serviceID);
                if (index !== -1) {
                    this.checkList.splice(index, 1);
                }
            }

            //Check When subcheck is checked all, so the Main Checkbox Label Name (all) that controls the subcheck return to true.
            if (this.checkList.length == this.serviceList.length) {
                this.isCheckAll = true;
            } else {
                this.isCheckAll = false;
            }
        },


        onResetServiceCheckedBox() {
            this.isCheckAll = false
            this.serviceList.forEach((item: any) => {
                return item.checked = false;
            })
        },


        onClickModalSave() {
            if (this.checkList.length > 0) {
                this.$dialog.onOpen({
                    dialogMode: "Question",
                    content: this.$t('message.would_you_like_to_save'),
                    callback: () => {
                        modalController.dismiss()
                        this.registerServiceRole();
                    }
                })
            } else {
                this.$dialog.onOpen({
                    dialogMode: "Warning",
                    content: this.$t('com206002sc00.please_select_a_service_before_clicking_the_save_button'),
                    callback: () => {
                        modalController.dismiss()
                    }
                })
            }
        },


        async registerServiceRole() {
            const reqBody: COM20600221_REQ_BODY = {
                list: this.checkList
            };
            const responseData = await this.$request.getRequest("COM20600221", reqBody, false) as COM20600221_RES;
            if (responseData.header.result) {
                this.onGetRoleList();
                this.checkList = []
                this.isAddService = false;
                this.onResetServiceCheckedBox();
                this.linkToRole = ''
                this.$dialog.onOpen({
                    dialogMode: "Success",
                    content: this.$t('com206002sc00.register_service'),
                    callback: () => {
                        modalController.dismiss()
                    }
                })
                this.onClickCancel();
            }
        },


        onCheckTreeList(value) {
            if (value.state.checked) {
                this.roleListForRemove.push(value)
            } else {
                const index = this.roleListForRemove.findIndex(e => e.employeeNo == value.employeeNo);
                if (index !== -1) {
                    this.roleListForRemove.splice(index, 1);
                }
            }

            // Disabled Tree CheckBox When add new Role
            this.selectedRole.isTempNewRole ? value.state.checked = null : value.state.checked;
        },


        onClickModalRemove() {
            if (this.selectedRole.userRoleName && this.roleListForRemove.length == 0) {
                this.$dialog.onOpen({
                    dialogMode: "Warning",
                    content: this.$t('com206002sc00.main_role_cannot_be_remove'),
                    callback: () => {
                        modalController.dismiss()
                        this.selectedRole.state.selected = false
                    }
                })
            } else {
                if (this.roleListForRemove.length !== 0) {
                    this.$dialog.onOpen({
                        dialogMode: "Question",
                        content: this.$t('com206002sc00.are_you_sure_you_want_to_remove_service_from_role'),
                        callback: () => {
                            this.removeServiceFromRole()
                        }
                    })
                } else {
                    this.$dialog.onOpen({
                        dialogMode: "Warning",
                        content: this.$t('com206002sc00.please_check_list_before_Remove'),
                        callback: () => {
                            modalController.dismiss()
                        }
                    })
                }
            }
        },


        async removeServiceFromRole() {
            this.roleServiceList = this.roleListForRemove.map((list: any) => {
                return { userRoleID: list.parentUserRoleID, serviceID: list.serviceID }
            })
            const reqBody: COM20600241_REQ_BODY = {
                list: this.roleServiceList
            };
            const responseData = await this.$request.getRequest("COM20600241", reqBody, false) as COM20600241_RES;
            if (responseData.header.result) {
                this.onGetRoleList();
                this.roleServiceList = [];
                this.roleListForRemove = [];
                this.clearForm()
                this.$dialog.onOpen({
                    dialogMode: 'Success',
                    content: this.$t('com206002sc00.remove_service_success'),
                    callback: () => modalController.dismiss()
                })
            }
        },


        handlePagination(pageNumber: number) {
            this.currentPage = pageNumber;
            this.isReset = false;
            this.onGetServiceList();
        },


        handleStartingIndex(startingIndex: number) {
            this.startingIndex = startingIndex;
        },


        onHighLightParentNode() {
            if (this.selectedRole.isTempNewRole) {
                const parentIndex = this.roleTree.findIndex(
                    (list) => list.userRoleID == this.selectedRole.parentUserRoleID
                );
                if (parentIndex > -1) {
                    this.roleTree[parentIndex].state.highLightParentNode = true;
                }
            }
        },


        onResetHighLightParentNode() {
            const parentIndex = this.roleTree.findIndex(
                (list) => list.userRoleID == this.selectedRole.parentUserRoleID
            );
            if (parentIndex > -1) {
                this.roleTree[parentIndex].state.highLightParentNode = false;
            }
        },


        clearForm() {
            this.linkToRole = ''
            this.searchServiceIDName = ''
            this.searchByServiceID = ''
        },

        async approvalRequest() {
            if (this.approvalType != '') {
                await this.$approval.getAuthorizationDetail().then(async (authDetail: APM20200113_RES) => {
                    let authDetailParsed = JSON.parse(authDetail.body.requestContent)
                    this.approvalRes = authDetail.body
                    this.operationMode = this.approvalType == 'my-request' ? 'read' : this.approvalType == 'my-approval' ? 'approval' : 'normal';
                    if (this.$util.checkServiceIdType(authDetail.body.serviceID) == 'delete') {
                        this.isApprovalDeletedStatus = true;
                        console.log("authDetailParsed.list", authDetailParsed.list);
                        authDetailParsed.list.forEach((item: any) => {
                            const itemTree = this.flattenTree(this.roleTree);
                            itemTree.find((tree) => {
                                if (item.userRoleID == tree.parentUserRoleID) {
                                    if (item.serviceID == tree.serviceID) {
                                        tree.state = { indeterminate: false, checked: true, selected: true, expanded: false }
                                        this.cloneServiceRoleData.push({
                                            serviceID: tree.serviceID,
                                            serviceIDName: tree.serviceIDName,
                                            parentUserRoleID: tree.parentUserRoleID
                                        })
                                    }
                                }
                            });
                            setTimeout(() => {
                                let treeParentClass: any = document.querySelector('#tree_active');
                                if (treeParentClass) {
                                    const activeChildrenClass = treeParentClass.querySelector('.active');
                                    if (activeChildrenClass) {
                                        activeChildrenClass.scrollIntoView({ behavior: "smooth", block: "center" });
                                    }
                                }
                            }, 50);
                        })
                    } else {
                        const reqBody: COM20600212_REQ_BODY = {
                            serviceID: '',
                            serviceIDName: '',
                            pageNumber: 1,
                            pageSize: 50000,
                            userRoleID: authDetailParsed.list[0].userRoleID
                        };
                        const responseData = await this.$request.getRequest("COM20600212", reqBody, false) as COM20600212_RES;
                        if (responseData.header.result) {
                            const bankStaffList = responseData.body.list as any;
                            authDetailParsed.list.find((auth) => {
                                bankStaffList.find((item) => {
                                    if (auth.serviceID == item.serviceID) {
                                        this.cloneServiceRoleData.push({
                                            serviceID: item.serviceID,
                                            serviceIDName: item.serviceIDName,
                                            parentUserRoleID: auth.userRoleID
                                        })
                                    }
                                })
                            })
                            this.roleList = [...this.roleList, ...this.cloneServiceRoleData];
                            this.transformAndExpandTreeData(this.roleList)
                            const treeList = this.flattenTree(this.roleTree);
                            treeList.find((item) => {
                                this.cloneServiceRoleData.find((list) => {
                                    if (item.serviceID == list.serviceID && list.parentUserRoleID == item.parentUserRoleID) {
                                        item.state = { indeterminate: false, checked: false, selected: true, expanded: false };
                                        return;
                                    }
                                })
                            });
                            setTimeout(() => {
                                let treeParentClass: any = document.querySelector('#tree_active');
                                if (treeParentClass) {
                                    const activeChildrenClass = treeParentClass.querySelector('.active');
                                    if (activeChildrenClass) {
                                        activeChildrenClass.scrollIntoView({ behavior: "smooth", block: "center" });
                                    }
                                }
                            }, 50);
                        }
                    }
                });
            }
        },

        apprrovalRoleActive() {
            const treeList = this.flattenTree(this.roleTree);
            treeList.find((item) => {
                this.cloneServiceRoleData.find((list) => {
                    if (item.serviceID == list.serviceID && list.parentUserRoleID == item.parentUserRoleID) {
                        this.isApprovalDeletedStatus ? item.state = { indeterminate: false, checked: true, selected: true, expanded: false } : item.state = { indeterminate: false, checked: false, selected: true, expanded: false };
                        return;
                    }
                })
            })
        },


        flattenTree(tree: any) {
            const flattenedTree = [] as any
            tree.forEach((node: any) => {
                flattenedTree.push(node);
                if (node.nodes.length > 0) {
                    const childNodes = this.flattenTree(node.nodes);
                    flattenedTree.push(...childNodes);
                }
            });
            return flattenedTree;
        },

    },

});
